<template>
  <div id="LogoBar">
    <ul id="right" class="navList">
      <li
        id="logo"
        onclick="location.href='https://www.mi.com/index.html'"
      ></li>
      <li class="logo-right" v-for="(item, index) in navListSec" :key="index">
        {{ item.label }}
      </li>
    </ul>
    <div class="searchInput">
      <input type="text" placeholder="请输入您要搜索的内容..." />
      <button type="submit"></button>
    </div>
  </div>
</template>、

<script>
export default {
  data: function () {
    return {
      navListSec: [
        {
          label: 'Xiaomi手机',
          linkUrl: 'www.baidu.com'
        },
        {
          label: 'Redmi红米',
          linkUrl: 'www.baidu.com'
        },
        {
          label: '电视',
          linkUrl: 'www.baidu.com'
        },
        {
          label: '笔记本',
          linkUrl: 'www.baidu.com'
        },
        {
          label: '平板',
          linkUrl: 'www.baidu.com'
        },
        {
          label: '家电',
          linkUrl: 'www.baidu.com'
        },
        {
          label: '路由器',
          linkUrl: 'www.baidu.com'
        },
        {
          label: '服务',
          linkUrl: 'www.baidu.com'
        },
        {
          label: '社区',
          linkUrl: 'www.baidu.com'
        }
      ]
    }
  }
}
</script>

<style scoped>
#LogoBar {
  /* overflow: hidden; */
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.searchInput {
  position: relative;
  width: 300px;
}

input,
button {
  border: none;
  outline: none;
}

input {
  width: 100%;
  height: 42px;
  /* padding-left: 13px; */
}

input::-webkit-input-placeholder {
  text-indent: 10px;
  font-size: 16px;
  color: #b4afaf;
}

button {
  height: 44px;
  width: 44px;
  cursor: pointer;
  position: absolute;
}

.searchInput input {
  border: 1px solid #7ba7ab;
  border-radius: 5px;
  background: #f9f0da;
  color: #9e9c9c;
}
.searchInput button {
  top: 0;
  right: -1px;
  background: #7ba7ab;
  border-radius: 0 5px 5px 0;
}
.searchInput button:before {
  content: "\f002";
  font-family: FontAwesome;
  font-size: 16px;
  color: #f9f0da;
}

#logo {
  height: 56px;
  width: 56px;
  background-image: url(//s02.mifile.cn/assets/static/image/logo-mi2.png);
  background-position: left center;
  background-size: 56px;
  background-repeat: no-repeat;

  margin-right: 100px;
}

#right {
  /* display: inline-block; */

  height: 96px;
}

ul {
  /* height: 96px; */
  line-height: 96px;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

li {
  display: inline-block;
}

.logo-right {
  height: 100%;
  padding: 0 10px;

  cursor: pointer;
  font-size: 16px;
}

.logo-right:hover {
  color: orange;
}
</style>



