<template>
  <div class="ScrollTop">
    <ul class="fixedItems">
      <li>
        <span class="icon-upload2"></span>
        <p>手机app</p>
      </li>
      <li>
        <span class="icon-upload2"></span>
        <p>个人中心</p>
      </li>
      <li>
        <span class="icon-upload2"></span>
        <p>售后服务</p>
      </li>
      <li>
        <span class="icon-upload2"> </span>
        <p>人工客服</p>
      </li>
      <li>
        <span class="icon-upload2"></span>
        <p>购物车</p>
      </li>
    </ul>
    <div class="scrolltotop" @click="backTop">
      <span class="icon-upload2"></span>
      <p>回到顶部</p>
    </div>
  </div>
</template>

<style scoped>
.ScrollTop {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  position: fixed;
  right: 10px;
  bottom: 10px;
  z-index: 10;

  background-color: #fff;
  box-shadow: 2px 2px 5px #bbb;
}

ul li {
    display: flex;
}

.scrolltotop {
    display: none;
    margin-top: 10px;
}

ul li,
.scrolltotop {
  height: 92px;
  width: 84px;

  border: 1px solid #f5f5f5;

  color: rgb(110, 105, 105, 0.8);

  flex-direction: column;
  justify-content: center;
  align-items: center;
}
</style>

<script>
export default {
  methods: {
    backTop () {
      var scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      var speed = 33
      function returnTopOnce () {
        if (scrollTop > speed) {
          window.scrollTo(0, scrollTop)
          scrollTop -= speed
          return
        }
        clearInterval(timer)
        window.scrollTo(0, 0)
      }
      var timer = setInterval(returnTopOnce, 1000 / 60)
    }
  },
  mounted () {
    window.onscroll = function () {
      console.log('111111')
      if (document.body.scrollTop > 100 || document.documentElement.scrollTop > 100) {
        console.log(document.querySelector('.scrolltotop'))
        document.querySelector('.scrolltotop').style.display = 'flex'
      } else {
        document.querySelector('.scrolltotop').style.display = 'none'
      }
    }
  }
}
</script>



