<template>
  <div class="sidebar">
    <div class="sidebarLeft">
      <router-link
        class="sidebarItem"
        to="#"
        v-for="(item, index) in sideBarList"
        :key="index"
        >{{ item.label }}</router-link
      >
    </div>
    <div class="sidebarRight">
      <swiper ref="mySwiper" :options="swiperOptions">
        <swiper-slide>
          <img
            src="https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/f89261d8655d76f2ab2340e4a3738678.jpg?w=2452&h=920"
            alt=""
            style="object-fit:cover"

          />
        </swiper-slide>
        <swiper-slide>
          <img
            src="https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/a532e33470d046b3f044d5ea49fc5e9e.png?thumb=1&w=1226&h=460&f=webp&q=90"
            alt=""
          />
        </swiper-slide>
        <swiper-slide>
          <img
            src="https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/73927a6ef08dacc1b828b8fa8b1727c3.jpeg?thumb=1&w=1226&h=460&f=webp&q=90"
            alt=""
          />
        </swiper-slide>
        <div class="swiper-button-prev" slot="button-prev"></div>
        <!--左箭头。如果放置在swiper外面，需要自定义样式。-->
        <div class="swiper-button-next" slot="button-next"></div>
        <!--右箭头。如果放置在swiper外面，需要自定义样式。-->
        <!-- <div class="swiper-pagination" slot="pagination"></div> -->

      </swiper>
    </div>
    <!-- 导航等组件可以放在Swiper容器之外 -->
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'

// import 'swiper/swiper-bundle.css'

// import style (<= Swiper 5.x)
import 'swiper/css/swiper.css'

export default {
  components: {
    Swiper,
    SwiperSlide
  },
  name: 'carrousel',

  data: function () {
    return {
      swiperOptions: {
        pagination: {
          el: '.swiper-pagination'
        },
        // Some Swiper option/callback...
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
        loop: true
      },
      sideBarList: [
        {
          label: '手机',
          linkUrl: 'www.baidu.com'
        },
        {
          label: '电视',
          linkUrl: 'www.baidu.com'
        },
        {
          label: '笔记本 平板',
          linkUrl: 'www.baidu.com'
        },
        {
          label: '家电',
          linkUrl: 'www.baidu.com'
        },
        {
          label: '出行 穿戴',
          linkUrl: 'www.baidu.com'
        },
        {
          label: '智能 路由器',
          linkUrl: 'www.baidu.com'
        },
        {
          label: '电源 配件',
          linkUrl: 'www.baidu.com'
        },
        {
          label: '健康 儿童',
          linkUrl: 'www.baidu.com'
        },
        {
          label: '耳机 音箱',
          linkUrl: 'www.baidu.com'
        },
        {
          label: '生活 音箱',
          linkUrl: 'www.baidu.com'
        }
      ]
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.$swiper
    }
  },
  mounted () {
    console.log('Current Swiper instance object', this.swiper)
    // this.swiper.slideTo(1, 1000, false)
  }
}
</script>

<style scoped>
img {
    height: 460px;
    /* object-fit: cover; */
}
.swiper {
  --swiper-theme-color: #ff6600; /* 设置Swiper风格 */
  --swiper-navigation-color: #00ff33; /* 单独设置按钮颜色 */
  --swiper-navigation-size: 30px; /* 设置按钮大小 */
}
.swiper-button-prev {
    margin-left: 234px;
}

.sidebar {
  /* display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start; */
  position: relative;
}
.sidebarLeft {
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 10;

  width: 234px;
  background: gray;
}

.sidebarRight {
  width: 100%;
  height: 460px;
}

.sidebarItem {
  display: block;
  height: 46px;
  color: white;
  text-decoration: none;
  /* font-size: 18px; */
  /* text-indent: 20px; */
  padding-left: 20px;
  line-height: 50px;
}

.sidebarItem:hover {
  color: white;
  background-color: orange;
}
</style>



