<template>
  <div id="home">
    <head-bar></head-bar>
    <div id="main">
      <logo-bar></logo-bar>
      <side-bar></side-bar>
      <recommend-bar id="recommend"></recommend-bar>
      <key-product id="keyproduct"></key-product>
      <scroll-top></scroll-top>
      <video-items></video-items>
    </div>
    <div id="footer">
      <div id="footer-items">
        <footer-service></footer-service>
        <footer-links></footer-links>
      </div>
    </div>
    <div class="fixedBtn"></div>
  </div>
</template>

<script>
import HeadBar from '@/components/HeadBar'
import LogoBar from '@/components/LogoBar'
import SideBar from './components/SideBar'
import RecommendBar from '@/components/RecommendBar'
import KeyProduct from '@/components/KeyProduct'
import ScrollTop from '@/components/ScrollToTop'
import FooterService from '@/components/FooterService'
import FooterLinks from '@/components/FooterLinks'
import VideoItems from '@/components/Video'

export default {
  components: {
    HeadBar,
    LogoBar,
    SideBar,
    RecommendBar,
    KeyProduct,
    ScrollTop,
    FooterService,
    FooterLinks,
    VideoItems
  }
}
</script>
<style scoped>
#main,
#footer-items {
  width: 1226px;
  margin: 0 auto;
}

#recommend,
#keyproduct {
  margin-top: 10px;
}

#footer {
  /* height: 800px; */
}
</style>



