<template>
  <div class="RecommendBar">
    <div class="first">
      <ul class="firstItem">
        <li v-for="(item, index) in datalist" :key="index">
          <a href="www.baidu.com"
            ><img :src="item.Src" alt="" />
            <p>{{ item.Name }}</p></a
          >
        </li>
      </ul>
    </div>
    <div class="second">
      <img
        src="https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/d8a6d6d37904e22c72130e3e4ec79b41.jpg?w=632&h=340"
        alt="#"
      />
    </div>
    <div class="third">
      <img
        src="https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/0bdb0b3ff4e73fe2a6bf2f8fd399015e.png?w=632&h=340"
        alt=""
      />
    </div>
    <div class="fourth">
      <img
        src="https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/35b3f906861db9d6472206e6d68318d9.jpeg?w=632&h=340"
        alt=""
      />
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      datalist: [
        {
          Src: 'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/82abdba456e8caaea5848a0cddce03db.png?w=48&h=48',
          Name: '保障服务'
        },
        {
          Src: 'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/eded6fa3b897a058163e2485532c4f10.png?w=48&h=48',
          Name: '企业团购'
        },
        {
          Src: 'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/43a3195efa6a3cc7662efed8e7abe8bf.png?w=48&h=48',
          Name: 'F码通道'
        },
        {
          Src: 'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/f4846bca6010a0deb9f85464409862af.png?w=48&h=48',
          Name: '米粉卡'
        },
        {
          Src: 'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/9a76d7636b08e0988efb4fc384ae497b.png?w=48&h=48',
          Name: '以旧换新'
        },
        {
          Src: 'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/9a76d7636b08e0988efb4fc384ae497b.png?w=48&h=48',
          Name: '话费充值'
        }
      ]
    }
  }
}
</script>

<style scoped>
.RecommendBar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.firstItem {
  height: 170px;
  width: 234px;
  background: rgba(75, 71, 72, 0.8);

  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;

  overflow: hidden;

}

.firstItem img {
  width: 24px;
  height: 24px;
  margin: 0 auto;
}

.second {
  height: 170px;
  width: 316px;
}

.second img {
  height: 170px;
  width: 316px;
}

.third {
  height: 170px;
  width: 316px;
}

.third img {
  height: 170px;
  width: 316px;
}

.fourth {
  height: 170px;
  width: 316px;
}

.fourth img {
  height: 170px;
  width: 316px;
}

li {
  position: relative;
  /* margin-right: 5px; */

  width: 76px;
  height: 82px;
}

li::before {
  top: -1px;
  left: 6px;
  width: 64px;
  height: 1px;

  position: absolute;
  content: "";
  background: #665e57;
}

li::after {
  top: 6px;
  left: 0;
  width: 1px;
  height: 70px;

  position: absolute;
  content: "";
  background: #665e57;
}

a {
  padding-top: 18px;
  color: rgba(255, 255, 255, 0.6);
  text-decoration: none;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

a::before {
}
</style>



